import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import { getWindowSize } from '../../utils';
import { GLOBAL_VARS } from '../../utils/constants';

class EcosystemAnim {
	articleAnim() {
		const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
		const $article = document.querySelector('.ecosystemArticle');
		if (!$article) return;

		const $articleTitle = $article.querySelector('.ecosystemArticleTitle');
		const $articleDecor = $article.querySelector('.ecosystemArticleDecor');
		const $articleFade = $article.querySelectorAll('.ecosystemArticleFade p');
		const $articleContent = $article.querySelector('.ecosystemQuoteContent');
		const $articleBg = $article.querySelector('.ecosystemQuoteBg');
		const $articleBg2 = $article.querySelector('.ecosystemQuoteBg2');
		const $articleBgPng = $article.querySelector('.ecosystemQuoteBgPng');
		const $articleBgPng2 = $article.querySelector('.ecosystemQuoteBgPng2');
		const $articleBgWrap = $article.querySelector('.ecosystemQuoteBgWrap');
		const $articleBgWrapIn = $article.querySelector('.ecosystemQuoteBgWrapIn');
		const $articleQuoteDecor = $article.querySelector('.ecosystemQuoteDecor');

		const articleTl = gsap.timeline({
			delay: 0.2,
		});

		const quoteTl = gsap.timeline();

		gsap.set($articleTitle, {
			opacity: 0,
			filter: 'blur(2px)',
		});
		// gsap.set($articleBgWrap, {
		// 	clipPath: 'polygon(99% 0, 100% 0, 100% 100%, 99% 100%)',
		// });
		gsap.set([$articleBgWrap, $articleBgWrapIn], {
			xPercent: gsap.utils.wrap([100, -100]),
		});
		if (isSafari) {
			gsap.set([$articleBg, $articleBg2], {
				opacity: gsap.utils.wrap([0, 0]),
			});
			gsap.set([$articleBgPng, $articleBgPng2], {
				opacity: gsap.utils.wrap([1, 0]),
			});
		} else {
			gsap.set([$articleBg, $articleBg2], {
				opacity: gsap.utils.wrap([1, 0]),
			});
			gsap.set([$articleBgPng, $articleBgPng2], {
				opacity: gsap.utils.wrap([0, 0]),
			});
		}
		gsap.set($articleContent, {
			x: 100,
			opacity: 0,
		});
		gsap.set([$articleQuoteDecor, $articleDecor], {
			scaleY: 0,
			transformOrigin: 'top',
		});
		gsap.set($articleFade, {
			opacity: 0,
			y: 25,
		});

		articleTl
			.addLabel('start')
			.to($articleTitle, {
				opacity: 1,
				duration: 0.4,
				ease: 'power2.inOut',
			}, 'start')
			.to($articleTitle, {
				filter: 'blur(0px)',
				duration: 0.4,
				ease: 'power2.inOut',
			}, 'start')
			.addLabel('fade')
			.to($articleFade, {
				opacity: 1,
				duration: 0.4,
				stagger: 0.4,
				ease: 'power1.inOut',
			}, 'fade')
			.to($articleFade, {
				y: 0,
				duration: 0.4,
				stagger: 0.4,
				ease: 'power1.inOut',
			}, 'fade')
			.to($articleDecor, {
				scaleY: 1,
				duration: 0.4,
				ease: 'power1.inOut',
			})
			.addLabel('end');

		const handleEnter = () => {
			if (isSafari) {
				quoteTl
					// .to($articleBgWrap, {
					// 	clipPath: 'polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)',
					// 	duration: 0.7,
					// 	ease: 'power1.inOut',
					// })
					.to([$articleBgWrap, $articleBgWrapIn], {
						xPercent: gsap.utils.wrap([0, 0]),
						duration: 0.7,
						ease: 'power1.inOut',
					})
					.to([$articleBgPng, $articleBgPng2], {
						opacity: gsap.utils.wrap([0, 1]),
						duration: 0.8,
						ease: 'none',
					}, '-=0.2')
					.addLabel('content')
					.to($articleContent, {
						opacity: 1,
						duration: 0.4,
						ease: 'power1.inOut',
					}, 'content-=.2')
					.to($articleContent, {
						x: 0,
						duration: 0.6,
						ease: 'power1.inOut',
					}, 'content-=.3')
					.to($articleQuoteDecor, {
						scaleY: 1,
						duration: 0.4,
						ease: 'power1.inOut',
					})
					.addLabel('end');
			} else {
				quoteTl
					// .to($articleBgWrap, {
					// 	clipPath: 'polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)',
					// 	duration: 0.7,
					// 	ease: 'power1.inOut',
					// })
					.to([$articleBgWrap, $articleBgWrapIn], {
						xPercent: gsap.utils.wrap([0, 0]),
						duration: 0.7,
						ease: 'power1.inOut',
					})
					.to([$articleBg, $articleBg2], {
						opacity: gsap.utils.wrap([0, 1]),
						duration: 0.8,
						ease: 'none',
					}, '-=0.2')
					.addLabel('content')
					.to($articleContent, {
						opacity: 1,
						duration: 0.4,
						ease: 'power1.inOut',
					}, 'content-=.2')
					.to($articleContent, {
						x: 0,
						duration: 0.6,
						ease: 'power1.inOut',
					}, 'content-=.3')
					.to($articleQuoteDecor, {
						scaleY: 1,
						duration: 0.4,
						ease: 'power1.inOut',
					})
					.addLabel('end');
			}
		};

		ScrollTrigger.create({
			trigger: $articleBgWrap,
			start: 'center bottom',
			onEnter: handleEnter,
		});
	}

	solutionsAnim() {
		const $solutions = document.querySelector('.solutions');

		if (!$solutions) return;

		const $solutionsItemTop = document.querySelectorAll('.solutionsItemTop');
		const $solutionsItemArrow = document.querySelectorAll('.solutionsItemArrow');

		const solutionsTl = gsap.timeline();

		gsap.set($solutionsItemTop, {
			opacity: 0,
			y: 35,
		});
		gsap.set($solutionsItemArrow, {
			opacity: 0,
			x: -8,
		});

		const handleEnter = () => {
			solutionsTl
				.to($solutionsItemTop, {
					y: 0,
					opacity: 1,
					duration: 0.5,
					stagger: 0.5,
					ease: 'power1.inOut',
				})
				.to($solutionsItemArrow, {
					x: 0,
					opacity: 1,
					duration: 0.4,
					stagger: 0.4,
					ease: 'power1.inOut',
				});
		};

		ScrollTrigger.create({
			trigger: $solutions,
			start: 'top+=20% bottom',
			onEnter: handleEnter,
		});
	}

	constructor() {
		this.init();
	}

	init() {
		if (getWindowSize().windowWidth < GLOBAL_VARS.mediaPoint1) return;

		gsap.registerPlugin(ScrollTrigger);

		this.articleAnim();
		this.solutionsAnim();
	}
}

export default EcosystemAnim;
